.entry__1,
.entry__2 {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	margin-top: 30px;
	margin-bottom: 30px;
}
.entry__2 {
	flex-direction: row-reverse;
}
.entry__avatar--1,
.entry__avatar--2 {
	width: 150px;
	height: 150px;
	display: inline-block;
}
.entry__avatar--1 {
	background: transparent url(/assets/avatar_2.svg) 98% 50% / 150px 150px no-repeat;
}
.entry__avatar--2 {
	background: transparent url(/assets/avatar_1.svg) 98% 50% / 150px 150px no-repeat;
}
.entry__item--1,
.entry__item--2 {
	position: relative;
	min-width: 300px;
	width: calc(100% - 30px);
	height: auto;
	padding: 15px;
	background: #FFFFFF;
	-webkit-border-radius: 0px;
	-moz-border-radius: 0px;
	border-radius: 0px;
	border: $whiteMagic solid 3px;
	text-align: justify;
	display: flex;
	flex-direction: column;
	align-content: stretch;
}
.entry__item--1:after,
.entry__item--2:after,
.entry__item--1:before,
.entry__item--2:before {
	content: '';
	position: absolute;
	border-style: solid;
	display: block;
	width: 0;
	top: 50%;
}
.entry__item--1:after,
.entry__item--2:after {
	border-color: transparent #FFFFFF;
	z-index: 1;
	margin-top: -12px;
}
.entry__item--1:before,
.entry__item--2:before
{
	border-color: transparent $whiteMagic;
	z-index: 0;
	margin-top: -14px;
}
.entry__item--1:after
{
	border-width: 12px 0 12px 26px;
	right: -26px;
}
.entry__item--2:after
{
	border-width: 12px 26px 12px 0;
	left: -26px;
}
.entry__item--1:before
{
	border-width: 14px 0 14px 28px;
	right: -31px;
}
.entry__item--2:before
{
  border-width: 14px 28px 14px 0;
  left: -31px;
}
.entry__item--1 span,
.entry__item--2 span {
	width: 100%;
}
span.entry__item--comment {
	flex:1;
	font-size: 1.5em;
}
span.entry__item--created_date {;
	display: flex;
	align-content: flex-end;
	justify-content: flex-end;
	height: 1em;
}



