/*********BEGIN BASE ELEMENTS**********/
body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
html, body {
  margin: 0;
  padding: 0;
  min-width: 230px;
  max-width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  font-smoothing: antialiased;
  font-weight: 300;
	background-color: $pureEvil;
}
body.fix {
  position: fixed;
  width: 100%;
}
h1, h2, h3 {
  text-shadow: 1px 1px 3px rgba(60, 60, 60, 0.6);
  width: 100%;
  margin: 0;
  line-height: .85em;
}
h1 {
  font-family: 'NeutrafaceDisplayBold';
  color: #fff;
  user-select: none;
}
h2 {
  text-align: center;
  font-size: 2.5em;
  font-family: 'NeutrafaceTextBook';
  margin: 30px auto;
  color: #fff;
}
h3 {
  font-size: 2em;
}
h1.font__creepster,
h2.font__creepster,
h3.font__creepster {
  font-family: 'creepster_regular';
  margin-top: 0px;
}

p {
  margin: auto;
  padding: 0 20px;
  background: rgba(255, 255, 255, 0.6);
  font-family: 'NeutrafaceTextDemi';
  font-size: 2em;
  text-align: justify;
  line-height: 2em;
  border-radius: 6px;
}
a {
	text-decoration: none;
	color: $letThereBeLight;
}
ul {
	padding: 0;
	margin: 0;
}
li {
	list-style: none;
}
.responsive__guide {
  display: none;
  position: absolute;
  width: 100%;
  height: 20px;
  z-index: 10;
}



