/*********BEGIN MIXINS**********/

@mixin theFirstBite {
	font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
@mixin sellThatShit {
	font-family: 'NeutrafaceDisplayTitling';
	text-shadow: 1px 1px 3px rgba(60, 60, 60, 0.6);
}
@mixin whyBeNormal {
	font-family: 'bebas_neueregular';
	font-size: 2em;
}
@mixin endOfTime {
	font-family: 'bebas_neuelight';
	font-size: 1.5em;
}
@mixin plungeToTheAbyss {
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -moz-flex;
	display: -webkit-flex;
	display: flex;
}
@mixin beALoser {
	margin: 0;
	padding: 0;
}
@mixin suckYouDry($a, $b, $c, $d) {
	//#b40a1d, #48140f
	background: $a;
	/* For browsers that do not support gradients */
	background: -webkit-radial-gradient(circle, $a $b, $c $d);
	/* Safari 5.1 to 6.0 */
	background: -o-radial-gradient(circle, $a $b, $c $d);
	/* For Opera 11.6 to 12.0 */
	background: -moz-radial-gradient(circle, $a $b, $c $d);
	/* For Firefox 3.6 to 15 */
	background: radial-gradient(circle, $a $b, $c $d);
	/* Standard syntax */
}
@mixin evilShadow {
	-webkit-box-shadow: 0px 0px 18px 0px rgba(0, 0, 6, 0.66);
	-moz-box-shadow: 0px 0px 18px 0px rgba(0, 0, 6, 0.66);
	box-shadow: 0px 0px 18px 0px rgba(0, 0, 6, 0.66);
}



