footer {
  width: 100%;
  height: 90px;
  align-self: flex-end;
  display: flex;
  align-content: stretch;
  border-top: 1px solid #fff;
}
footer ul {
  height: auto;
  font-family: 'NeutrafaceTextBold';
  font-size: 1.25em;
}
footer ul.footer__item--menu {
  flex: .25;
}
footer ul.footer__item--menu > li {
  display: inline-block;
  border-left: 1px solid $letThereBeLight;
}
footer ul.footer__item--menu > li:nth-child(1) {
  border-left: 0;
}
footer ul.footer__item--menu > li > a {
  display: inline-block;
  padding: 30px 35px;
}
footer ul.footer__item--company_info {
  display: flex;
  flex: 1;
  align-items: center;
}
footer ul.footer__item--company_info li {
  flex: 1;
  text-align: right;
}
footer ul.footer__item--company_info li:nth-child(2) {
  flex: .25;
  text-align: center
}
footer ul.footer__item--company_info li:nth-child(2) > a {
  display: inline-block;
  width: 90px;
  height: 90px;
}
footer ul.footer__item--company_info li span.copyright {
  height: 100%;
  display: inline-block;
  //line-height: 96px;
  color: $letThereBeLight;
  user-select: none;
}
footer ul.footer__item--company_info li span.icon__logo--footer {
  width: 140px;
  height: 30px;
  display: block;
  background: url("https://s3-us-west-1.amazonaws.com/vellocet/images/jonnyzero-logo-footer-white.svg") 5px/50% no-repeat;
  background-size: 100px;
}
footer ul.footer__item--company_info li span.icon__logo--footer a {
  display: inline-block;
}
.menu__social {
  flex: .75;
  display: flex;
  align-items: center;
  margin-left: 25px;
}
.menu__social > li {
  display: inline-block;
}
.menu__social span {
  height: 40px;
  width: 60px;
  display: block;
  background-size: 40px;
  background-repeat: no-repeat;
  background-position: 50%;
}



