.cart {
  font-family: 'bebas_neuebold';

}
.cart__list--header,
.cart__list--item,
.cart__list--footer {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  width: 100%;
}
.cart__list--header {
  font-size: 2em;
  border-bottom: 3px dashed #000;
}
.cart__list--header li,
.cart__list--item li,
.cart__list--footer li {
  flex: 1;
  text-align: center;
}
.cart__list--item li {
  font-size: 1.5em;
  padding: 20px 0;
}
.cart__list--item li:last-child {
  width: 50px;
  flex: none;
  text-align: center;
}
.cart__list--footer li {
  font-size: 1.75em;
  text-align: left;
}
.cart__list--footer li:last-child {
  text-align: right;
}
.cart__list--item_remove_icon {
  height: 25px;
  display: block;
  background: transparent url('/assets/close-icon-dark.svg') 50% 50% / 35px 25px no-repeat;
}



