.pager__component--wrapper {
  margin-top: 25px;
}
.pager__contents > div {
  display: none;
}
.pager__contents > div.active {
  display: block;
}
.pager__buttons {
  display: flex;
  justify-content: center;
  font-variant: small-caps;
}
.pager__buttons > li {
  width: 25px;
  background-color: #999;
  color: #fff;
  border-bottom: 0px;
  padding: 15px 10px;
  font-family: 'NeutrafaceTextBold';
  font-size: 1.25em;
  margin-left: 2px;
  text-align: center;
  cursor: pointer;
  user-select: none;
}
.pager__buttons > li:first-child {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.pager__buttons > li:last-child {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.pager__buttons > li.pager__active {
  background-color: #666;
}



