.order {
  display: flex;
  flex-direction: row;
}
.order li {
  flex: 1;
}
.order li:nth-child(1) {
  margin: 30px;
}
.order__images {
  width: 150px;
  height: 150px;
  display: inline-block;
  background-repeat: no-repeat;
  background-position-y: 50%;
  background-size: 100%;
}
.order__pricing {
  font-size: 3em;
  font-family: 'bebas_neuebold';
}
.order__tracking {
  display: flex;
  align-items: stretch;
  font-family: 'bebas_neuebold';
  font-size: 2em;
}
.progressbar {
  display: flex;
  align-items: stretch;
  height: 20px;
  background-color: #FFF;
  box-shadow: 0 0 6px 0 rgba(0, 0, 6, 0.66);
}
.tracking__details {
  font-family: 'bebas_neuebold';
  font-size: 2em;
}
.progressbar--step--in_progress {
  flex: 1;
  background:red;
  animation:myfirst 5s;
  -moz-animation:myfirst 5s infinite; /* Firefox */
  -webkit-animation:myfirst 5s infinite; /* Safari and Chrome */
}
@-moz-keyframes myfirst {
0%   {background:#CCC;}
50%  {background:#333;}
100%   {background:#CCC;}
}
@-webkit-keyframes myfirst {
0%   {background:#CCC;}
50%  {background:#333;}
100%   {background:#CCC;}
}
.progressbar--step--done {
  flex: 1;
  background-color: #000;
  display: inline-block;
}
.progressbar--step {
  flex: 1;
}
.order__tracking--step,
.order__tracking--status {
  flex: 1;
  text-align: center;
}
.order__tracking--success_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-success.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--payment_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-cc.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--order_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-tshirt.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--delivery_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-delivery.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--in_progress_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-hourglass.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--happy_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-face-happy.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--meh_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-face-meh.svg) 50% 50%/50px 50px no-repeat;
}
.order__tracking--sad_icon {
  width: 100%;
  height: 100px;
  display: block;
  background: transparent url(/assets/icon-face-sad.svg) 50% 50%/50px 50px no-repeat;
}
.tracking__details--item {
  margin: 20px;
  list-style-type: decimal;
}



