.shop_detail {
  display: flex;
  flex-direction: row;
  margin-top: 30px;
  font-family: 'NeutrafaceTextBold';
  color: #333;
}
.shop_detail li {
  flex: 1;
}
.shop_detail > li:nth-child(1) {
  margin-right: 30px;
}
.shop_detail__images {
  width: 100%;
  height: 100%;
  display: inline-block;
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 50%;
  background-size: 90%;
}
.shop_detail__images:hover {
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 50%;
  background-size: 200%;
}
.shop_detail__name {
  font-size: 3em;
}
.shop_detail__pricing {
  font-size: 2em;
}
.shop_detail__description {
  margin-top: 20px;
  display: block;
  font-family: 'NeutrafaceDisplayMedium';
  font-size: 2em;
}



