.inventory__list {
  display: flex;
  flex-direction: column;
}

.inventory__list > li {
  height: 60px;
  flex: 1;
  display: flex;
  text-align: left;
  -ms-flex-item-align: center;
  -ms-grid-row-align: center;
  color: #fff;
  border-bottom: 0px;
  font-family: 'NeutrafaceTextBold';
  font-size: 1.5em;
  border-radius: 6px;
  border: 1px solid #fff;
  margin-bottom: 10px;
  background: #999;
  align-items: center;
}
li.dropzone-placeholder {
  border: none;
  background: transparent;
  height: 60px;
  text-align: center;
  font-size: 2em;
  &:before {
    content: "Drop here";
    width: 100%;
    color: #FFF;
  }
}
.inventory__list > li > span:nth-child(1) {
  width: 200px;
  margin-left: 20px;
}
form.form__myproduct.hide {
  display: none;
}
div.myproduct__toolbar {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.inventory__list > li:hover {
  span.inventory__list--reorder {
    display: block;
    width: 60px;
    height: 40px;
    background-image: url(/assets/icon-reorder-white.svg);
    background-repeat: no-repeat;
    background-size: 25px 25px;
    background-position: 50% 50%;
    cursor: grab;
  }
}



