#barchart {
}
svg {
}
#barchart div {
  font: 10px sans-serif;
  background-color: steelblue;
  text-align: right;
  padding: 3px;
  margin: 1px;
  color: white;
}
.chart {
  background: black;
  margin: 5px;
}
rect {
  stroke: #000;
  stroke-width: 3px;
  fill: #333;
}
#barchart text {
  fill: #CCC;
  font-family: 'bebas_neuebold';
  font-size: 3em;
}
.chart .label {
  font: 14px sans-serif;
  text-anchor: end;
}
.bar {
  fill: #333;
}
.bar:hover {
  fill: #000;
}
.bar__text {
  text-anchor: start;
  fill: #CCC;
}
.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}



