.cta__preorder h1 {
  font-size: 5em;
  color: $whiteMagic;
  margin-bottom: 30px;
  -webkit-font-smoothing: antialiased;
  text-shadow: 6px 6px 6px #000;
  font-family: 'NeutrafaceDisplayTitling';
  //letter-spacing: 0.02em;
}
.cta {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: 100%;
	background-position: center;
	background-repeat: no-repeat;
}
a.cta__link {
  display: inline-block;
  padding: 20px 60px;
  font: 2em/100% 'bebas_neuebold';
  color: $whiteMagic;
  text-shadow: 1px 1px 3px rgba(60, 60, 60, 0.6);
  cursor: pointer;
  outline: none;
  background-color: #333;
  border: 3px solid $whiteMagic;
}

a:hover.cta__link {
  background-color: $whiteMagic;
  border: 3px solid #000;
  color: #000;
}



