.alert__hide,
.cart_alert__hide {
  display: none;
}
.fetch_alert__fadein {
  animation: fadein .5s ease-in both .5s 1;
}
.fetch_alert__fadeout {
  animation: fadeout .25s ease-out both .25s 1;
}
.alert,
.fetch_alert,
.cart_alert {
  display: flex;
  position: relative;
  z-index: 3;
  padding: 20px;
  font: 2em/100% 'NeutrafaceTextDemi';
  color: #CCC;
  text-shadow: 1px 1px 3px rgba(60, 60, 60, 0.6);
  cursor: pointer;
  outline: none;
  background-color: rgba(51,51,51,0.6);
  border: 3px solid rgba(204,204,204,0.6);
}
.fetch_alert {
  position: fixed;
  width: calc(100% - 100px);
  margin: 25px;
  background-color: rgb(51,51,51);
}
@-webkit-keyframes fadein {
  0%, 100% {opacity: 0;}
  100% {opacity: 1;}
}

@keyframes fadein {
  0%, 100% {opacity: 0;}
  100% {opacity: 1;}
  }

@-webkit-keyframes fadeout {
  50% {opacity: 0;}
  0% {opacity: 1;}
}

@keyframes fadeout {
  100% {opacity: 0;}
  0% {opacity: 1;}
  }
.alert__item span.icon__close,
.cart_alert__item span.icon__bag {
  width: 30px;
  height: 30px;
  display: inline-block;
  right: 20px;
  position: absolute;
}
.alert__item span.icon__close {
	background: transparent url(/assets/close-icon-ccc.svg) 50% 50% / 30px 30px no-repeat;
}
.cart_alert__item span.icon__bag {
	background: transparent url(/assets/icon-bag.svg) 50% 50% / 30px 30px no-repeat;
}



