#paypal-button-container {
  width: calc(100% - 6px);
}

.button__paypal
 {
   width: 100%;
   height: 80px;
}

.paypal-button-content {
  width: 700px;
  height: 80px;
  padding: 15px 0;
  border: 3px solid rgb(4,52,133) !important;
  border-radius: 0;
  background-color: #eee;

}

#paypal-button,
.paypal-button-content {
  height: 100px !important;
  width: 700px;
  border: 1px solid blue;
  display: block;
}

.icon__payment_success {
  width: 100%;
  height: 400px;
  display: inline-block;
  background: transparent url(/assets/icon-success.svg) 50% calc(50% - 30px)/200px 200px no-repeat;
}



