.address {
  font-family: 'bebas_neuebold';
  display: flex;
}
.address__item--billing,
.address__item--shipping {
  flex: 1;
}
.address__item--shipping {
  margin-left: 20px;
}
.address__list--header,
.address__list--item,
.address__list--footer {
  display: flex;
  flex-direction: row wrap;
  align-items: stretch;
  width: 100%;
}
.address__list--header {
  font-size: 2em;
  border-bottom: 3px dashed #000;
}
.address__list--header li,
.address__list--item li,
.address__list--footer li {
  flex: 1;
  text-align: center;
}
.address__list--item li {
  font-size: 1.5em;
  padding: 20px;
  text-align: left;
}
li.shipping__same_as_billing {
  text-align: center;
}
.address__list--footer li {
  font-size: 1.75em;
  text-align: left;
}
.address__list--footer li:last-child {
  text-align: right;
}



