.shop__list {
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  -ms-box-orient: horizontal;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}
.shop__list a {
  flex: 1;
}
.shop__list li {
  width: 330px;
  height: 380px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: white;
  font-weight: bold;
  font-size: 2em;
  text-align: center;
  margin: 30px auto 0px;
  flex-direction: column;
}
.shop__list li > div {
  width: 100%;
}
.shop__list li > div:nth-child(1) {
  height: 300px;
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 33%;
  background-size: 250%;
}
.shop__list li:hover > div:nth-child(1) {
    background-repeat: no-repeat;
    background-position-x: 50%;
    background-position-y: 50%;
    background-size: 240px;
}
.shop__list li > div:nth-child(2) {
  height: 50px;
  color: #FFF;
  font-size: 24px;
  line-height: 50px;
  font-family: 'NeutrafaceDisplayTitling';
}
.shop__list li:focus {
  outline: none;
}
.shop__list li:hover > div:nth-child(1) {
  //background: no-repeat 20%/300% url('../assets/tees/1.png');
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 50%;
  background-size: 240px;
}
.shop__list li button {
  background: transparent;
  color: #FFF;
  text-shadow: none;
  font-family: 'NeutrafaceDisplayBold';
}
.shop__list li:hover button {
  color: #FFF;
  background-color: transparent;
}



