.carousel {
  height: calc(100vh - 198px);
  width: 100%;
  display: flex;
  overflow: hidden;
  background: radial-gradient(circle, #666 10%, #000 99.9%);
}

.carousel__buttons {
  width: calc(100% - 250px);
  display: flex;
  position: absolute;
  justify-content: center;
  align-self: flex-end;
  z-index: 2;
}

.carousel__buttons li {
  width: 35px;
  height: 35px;
  margin: 30px;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 6, 0.66);
  border-radius: 50%;
  border: 3px solid $blackSoul;
}

.carousel__button {
  background-color: $whiteMagic;
}

.carousel__button--active {
  background-color: $halfEvil;
}

.carousel__images {
  height: calc(100vh - 98px);
  width: calc(100% - 250px);
  overflow: hidden;
  cursor: pointer;
  position: absolute;
}

.carousel__images li {
  width: 100%;
  background-repeat: no-repeat;
  transition: .25s;
}

.carousel__image--show {
  position: absolute;
  background-repeat: no-repeat;
  height: 100vh;
  background-position: 50% 50%;
  background-size: 360%;
}



