.breadcrumb__content {
  width: 100%;
}
.breadcrumb__featured {
  text-align: center;
  color: #333;
}
.breadcrumb__featured_banner--top {
  padding: 10px 0;
  border-top: 3px solid #000;
}
.breadcrumb__featured_banner--top h1 {
  font-size: 3em;
}
.breadcrumb__featured_banner--bottom {
  margin: 10px 0;
}
.breadcrumb__featured_banner--bottom h2 {
  margin-bottom: 0;
  font-size: 2.5em;
}
.breadcrumbs__top,
.breadcrumbs__bottom {
  display: flex;
  width: 100%;
  flex-flow: row;
  background-color: #333;
  border-top: 3px solid #000;
  border-bottom: 3px solid #000;
}
.breadcrumbs__top li.breadcrumb__item,
.breadcrumbs__bottom li.breadcrumb__item {
  flex-basis: calc(50% - 2px);
  display: flex;
  flex-direction: column;
  min-height: 250px;
  align-items: center;
  justify-content: center;
  border-right: 3px solid #000;
  border-top: 0;
  border-bottom: 0;
  padding: 0px;
  text-align: center;
}
li.breadcrumb__item:nth-child(2) {
  border-right: 0;
}



