nav {
  width: 250px;
  background-color: $pureEvil;
  box-shadow: 0 0 18px 0 rgba(0,0,6,.66);
  position: relative;
  z-index: 2;
}
.icon__logo--nav {
  width: 250px;
  height: 130px;
  background: url('https://s3-us-west-1.amazonaws.com/vellocet/images/jonnyzero-logo.svg') 100% / 85% no-repeat;
  display: block;
  background-position-y: bottom;
  background-position-x: center;
}
.menu__top > a:focus {
  outline: none;
}
.menu__main > li {
  border-bottom: 1px solid #999;
}
.menu__main > li:last-child {
  border-bottom: none;
}
.menu__main > li a {
  display: block;
  user-select: none;
  font-family: 'NeutrafaceDisplayTitling';
  font-size: 1.75em;
  width: 100%;
  text-align: center;
  padding: 10px 0;
}
ul.menu__table--layout {
  display: table;
  width: auto;
  margin: 0 auto;
}
ul.menu__table--layout > li {
  display: table-cell;
  vertical-align: middle;
}
ul.menu__table--layout > li:first-child {
  text-align: right;
}
ul.menu__table--layout > li:last-child a {
  margin-left: 10px;
  text-align: left;
}
.account_menu--icon {
  margin-right: 10px;
  width: 40px;
  height: 40px;
  display: inline-block;
  background: transparent url(/assets/default_user_icon-white.svg) 50% 50%/40px 40px no-repeat;
}
.logout__menu--item {
    display: block;
    font-family: 'NeutrafaceDisplayTitling';
    user-select: none;
    font-size: 1.75em;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    color: #fff;
    cursor: pointer;
}



