.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 4;
}
.modal__content {
  position: relative;
  z-index: 5;
  margin: 20px;
  font-family: 'bebas_neuebold';
  border: 3px solid #000;
  background-color: #CCC;
}
.modal__content--header {
  display: flex;
  width: 100%;
  border-bottom: 3px solid #000;
}
.modal__content--title {
  flex: 1;
  margin-bottom: 20px;
  margin-left: 20px;
}
.modal__content--close_icon {
  margin: 20px 0px auto auto;
}
.modal__title {
  margin-top: 20px;
  font-family: 'bebas_neuebold';
  text-shadow: 1px 1px 3px rgba(60, 60, 60, 0.6);
  font-size: 2em;
}
.modal__content--icon {
  width: 50px;
}
.modal__content--close_icon span.modal__content--icon__close {
  width: 25px;
  height: 25px;
  display: block;
  margin-right: 20px;
  background: transparent url('/assets/close-icon-dark.svg') 50% 50% / 35px 25px no-repeat;
}
.modal__content--body {
  padding: 20px;
}
.modal__content--footer {
  padding: 20px;
}
.modal__background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 4;
  background-color: rgba(51,51,51,0.6);
}
.modal__hide {
  display: none;
}
.modal__content--body .validator__message--show {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}



