.header__toolbar {
  display: flex;
}
.header__toolbar > h1 {
  flex: 1;
}
.header__toolbar > ul {
  display: flex;
}
.header__toolbar > ul > li {
  width: 40px;
  height: 20px;
  text-align: right;
  align-self: center;
  color: #fff;
  border-bottom: 0px;
  padding: 15px 10px;
  font-family: 'NeutrafaceTextBold';
  font-size: 1.25em;
  text-align: center;
  cursor: pointer;
  border-radius: 0;
  border: 1px solid #fff;
  user-select: none;
}
.header__toolbar > ul > li.search {
  background: transparent url(/assets/icon-search-white.svg) 50% 50% / 30px 30px no-repeat;
}
.header__toolbar > ul > li.filter {
  background: transparent url(/assets/icon-filter-white.svg) 50% 50% / 30px 30px no-repeat;
}
.header__toolbar > ul > li:first-child {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  border-right: 0;
}
.header__toolbar > ul > li:last-child {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border-left: 0;
}
ul.search__menu {
  width: calc(100% - 58px);
  position: absolute;
  right: 0;
  display: none;
  margin: auto 29px;
  margin-top: -5px;
  font-family: 'NeutrafaceTextBold';
}
ul.search__menu.show {
  display: block;
}
ul.search__menu > li:first-child {
  background-color: #FFF;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border: 1px solid #FFF;
}
ul.search__menu--results {
  position: absolute;
  background: white;
  z-index: 10;
  width: calc(100%);
  margin-top: 15px;
  border-radius: 6px;

}
ul.search__menu--results > li {
  padding: 15px;
  color: #666;
  font-size: 1.25em;
  border-bottom: 1px solid #CCC;
}
ul.search__menu--results > li a {
  color: inherit;
  display: table;
}

ul.search__menu--results > li a span {
  display: table-cell;
  vertical-align: middle;
  padding-left: 20px;
}
ul.search__menu > li:first-child {
  padding: 15px;
}
ul.search__menu--results > li:last-child {
  border: 0;
}
ul.filter__menu {
  width: 220px;
  position: absolute;
  right: 30px;
  background-color: #FFF;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  display: none;
  border: 1px solid #FFF;
  margin-top: -5px;
}
ul.filter__menu.show {
  display: block;
}
ul.filter__menu > li {
  padding: 15px;
  color: #666;
  font-family: 'NeutrafaceTextBold';
  font-size: 1.25em;
  border-bottom: 1px solid #CCC;
}
ul.filter__menu > li span {
  display: block;
  cursor: pointer;
}
ul.filter__menu > li:last-child {
  border-bottom: 0;
}

input.search__input {
  margin: 0;
}

ul.count__menu {
  width: 60px;
  position: absolute;
  right: 30px;
  background-color: #FFF;
  border-top-left-radius: 0;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  display: none;
  border: 1px solid #FFF;
  margin-top: -5px;
}
ul.count__menu.show {
  display: block;
}
ul.count__menu > li {
  padding: 15px;
  color: #666;
  font-family: 'NeutrafaceTextBold';
  font-size: 1.25em;
  border-bottom: 1px solid #CCC;
}
ul.count__menu > li > span {
  display: block;
  cursor: pointer;
}
ul.count__menu > li:last-child {
  border-bottom: 0;
}



